.left-chart-2 {
  width: 100%;
  height: 45%;
  display: flex;
  flex-direction: column;
}
.left-chart-2 .lc2-header {
  height: 20px;
  line-height: 20px;
  font-size: 16px;
  text-indent: 20px;
  margin-top: 10px;
}
.left-chart-2 .lc2-details {
  height: 40px;
  font-size: 16px;
  display: flex;
  align-items: center;
  text-indent: 20px;
}
.left-chart-2 .lc2-details span {
  color: #096dd9;
  font-weight: bold;
  font-size: 35px;
  margin-left: 20px;
}
.left-chart-2 .lc2-chart {
  height: calc(100% - 80px);
}
