#data-view {
  width: 100%;
  height: 100%;
  background-color: #030409;
  color: #fff;
}
#data-view #dv-full-screen-container {
  background-image: url(../../static/media/bg.110420cf.png);
  background-size: 100% 100%;
  box-shadow: 0 0 3px blue;
  display: flex;
  flex-direction: column;
}
#data-view .main-header {
  height: 80px;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}
#data-view .main-header .mh-left {
  font-size: 20px;
  color: #0186bb;
}
#data-view .main-header .mh-left a:visited {
  color: #0186bb;
}
#data-view .main-header .mh-middle {
  font-size: 30px;
}
#data-view .main-header .mh-left,
#data-view .main-header .mh-right {
  width: 480px;
}
#data-view .main-container {
  height: calc(100% - 80px);
}
#data-view .main-container .border-box-content {
  padding: 20px;
  box-sizing: border-box;
  display: flex;
}
#data-view .left-chart-container {
  width: 30%;
  padding: 10px;
  box-sizing: border-box;
}
#data-view .left-chart-container .border-box-content {
  flex-direction: column;
}
#data-view .right-main-container {
  width: 69%;
  padding-left: 5px;
  box-sizing: border-box;
  height: 100%;
}
#data-view .rmc-top-container {
  height: calc(100% - 50px);
  display: flex;
  flex-wrap: wrap;
}
#data-view .rmctc-left-container {
  width: 100%;
  height: 100%;
  padding: 2.5%;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  display: flex;
}
#data-view .rmctc-right-container {
  width: 35%;
}
#data-view .rmctc-chart-1,
#data-view .rmctc-chart-2 {
  height: 50%;
}
