.center-cmp {
  width: 100%;
  height: 100%;
  margin: 0px;
  padding: 0px;
  display: flex;
  flex-direction: column;
}
.center-cmp .cc-header {
  height: 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 15px;
  z-index: 9999;
}
.center-cmp .cc-details {
  height: 120px;
  display: flex;
  justify-content: center;
  font-size: 32px;
  align-items: center;
}
.center-cmp .cc-details .card {
  background-color: rgba(4, 49, 128, 0.6);
  color: #08e5ff;
  height: 70px;
  width: 70px;
  font-size: 45px;
  font-weight: bold;
  line-height: 70px;
  text-align: center;
  margin: 10px;
}
.center-cmp .cc-main-container {
  position: relative;
  flex: 1 1;
  display: flex;
}
.center-cmp .cc-main-container .ccmc-middle {
  width: 50%;
  height: 90%;
}
.center-cmp .cc-main-container .ccmc-middle .active-ring-name {
  font-size: 20px !important;
}
.center-cmp .cc-main-container .ccmc-left,
.center-cmp .cc-main-container .ccmc-right {
  width: 25%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: 24px;
}
.center-cmp .cc-main-container .ccmc-left span,
.center-cmp .cc-main-container .ccmc-right span {
  font-size: 40px;
  font-weight: bold;
}
.center-cmp .cc-main-container .ccmc-left .station-info,
.center-cmp .cc-main-container .ccmc-right .station-info {
  height: 80px;
  display: flex;
  align-items: center;
}
.center-cmp .cc-main-container .ccmc-left {
  align-items: flex-end;
}
.center-cmp .cc-main-container .ccmc-left span {
  margin-left: 20px;
}
.center-cmp .cc-main-container .ccmc-right {
  align-items: flex-start;
}
.center-cmp .cc-main-container .ccmc-right span {
  margin-right: 20px;
}
