.left-chart-1 {
  width: 100%;
  height: 45%;
  display: flex;
  flex-grow: 0;
  flex-direction: column;
}
.left-chart-1 .lc1-header {
  text-align: center;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 30px;
  margin-bottom: 20px;
}
.left-chart-1 .lc1-details {
  height: 50px;
  font-size: 16px;
  display: flex;
  align-items: center;
  text-indent: 20px;
}
.left-chart-1 .lc1-details span {
  color: #096dd9;
  font-weight: bold;
  font-size: 35px;
  margin-left: 20px;
}
.left-chart-1 .lc1-chart {
  flex: 1 1;
}
